@import 'vars';

.ProductItem {
	width: 240px;
}

.ProductItem:hover{
	background-color:whitesmoke;
	border-radius: 15px;
	box-shadow: 2px 2px 2px 2px #00000010;
	
	.product-info div p:nth-child(2){
		margin-left: 8px;
		color: #000;
	}
	
}

.ProductItem img {
	width: inherit;
	height: inherit;
	border-radius: 20px;
	object-fit: cover;
}
.product-info {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-top: 12px;
	
}
.product-info figure {
	margin: 0;
}
.product-info figure img {
	width: 35px;
	height: 35px;
}
.product-info div p:nth-child(1) {
	font-weight: bold;
	font-size: var(--md);
	margin-top: 0;
	margin-bottom: 4px;
}
.product-info div p:nth-child(2) {
	font-size: var(--sm);
	margin-top: 0;
	margin-bottom: 0;
	color: var(--very-light-pink);
}


@media (max-width: 640px) {
	.cards-container {
		grid-template-columns: repeat(auto-fill, 140px);
	}
	.ProductItem {
		width: 140px;
	}
	.ProductItem img {
		width: 140px;
		height: 140px;
	}
}
